.title {
    position: relative;
    font-family: GothamPro;
    font-size: 90px;
    font-weight: 700;
    line-height: 2;
    color: $white;
    position: relative;
    margin-bottom: 100px;
    &:after {
        content: '';
        position: absolute;
        top: 100%;
        left: 0;
        width: 300px;
        height: 10px;
        background-color: $red; } }

.type {
    font-family: GothamPro;
    font-size: 80px;
    font-weight: 500;
    color: $white; }

.title-big {
    font-size: 36px;
    font-weight: bold;
    line-height: 0.67;
    color: #2b2d42; }


