[data-modal] {
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  padding: 20px;
  opacity: 0;
  visibility: hidden;
  text-align: center;
  font-size: 0;
  white-space: nowrap;
  transform: translate3d(0, 0, 0);
  transition: opacity 0.3s, visibility 0.3s;
}

[data-modal]:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

[data-modal].is-open {
  opacity: 1;
  visibility: visible;
}

[data-modal-container] {
  position: relative;
  padding: 38px 58px;
  z-index: 1;
  min-width: 725px;
  max-width: 780px;
  max-height: 1140px;
  white-space: normal;
  text-align: left;
  font-size: 24px;
  color: #fff;
  vertical-align: middle;
  overflow: hidden;
  background-color: #fff;
  box-shadow: 0 0 25px rgba(43, 45, 66, 0.25);
  display: inline-block;
}

[data-modal-close] {
  position: absolute;
  top: 17px;
  right: 10px;
  padding: 20px;
  background-color: transparent;
  font-size: 0;
}

