.footer {
	height: 200px; }

.steps {
	position: relative;
	font-size: 0;
	counter-reset: section;
	&__item {
		opacity: 0.5;
		box-shadow: (inset 3px 0 0 rgba(255, 255, 255, 0.15) );
		vertical-align: top;
		display: inline-block;
		&.is-check {
			opacity: 1; }
		&.is-active {
			opacity: 1; } }
	&__link {
		position: relative;
		padding: 120px 47px 20px;
		color: $white;
		height: 200px;
		min-width: 415px;
		@include f(48px,500,1);
		display: block;
		z-index: 1;
		.steps__item.is-active & {
			color: $blue;
			background-color: $white; }
		&:before {
			position: absolute;
			top: 20px;
			left: 47px;
			color: #e32434;
			counter-increment: section;
			content: counter(section);
			@include f(72px,700,1);
			.steps__item:nth-of-type(-n+9) & {
				content: "0" counter(section) "."; } } }
	&__text {}
	&__check {
		position: absolute;
		top: 0;
		right: 0;
		height: 100px;
		width: 100px;
		text-align: center;
		line-height: 100px;
		background-color: $red;
		.icon {
			font-size: 38px;
			vertical-align: middle;
			display: inline-block; } }
	&__lock {
		position: absolute;
		top: 16px;
		right: 28px; }
	&__wrap {
		position: absolute;
		left: 0;
		bottom: 100%;
		padding: 50px 0 50px 50px;
		width: 2410px;
		height: 532px;
		background-color: $white;
		box-shadow: 0 0 25px rgba(43, 45, 66, 0.25);
		display: none;
		.steps__item.is-active & {
			display: block; } }
	&__layout {
		padding-right: 50px;
		height: 430px;
		display: block;
		.ps__rail-y {
			right: 35px !important; } }
	&__block {
		width: 300px;
		height: 200px; } }
