.menu[data-modal] {
    position: absolute;
    top: 72px;
    background-color: $white;
    padding: 48px 56px 51px;
    z-index: 100;
    right: auto;
    left: auto;
    bottom: auto;
    overflow: inherit;
    opacity: 0;
    visibility: hidden;
    white-space: nowrap;
    @include gpu;
    transition: opacity 0.3s, visibility 0.3s;
    .houses & {
        left: -56px; }
    .interiors & {
        right: -56px; }

    &.is-open {
        opacity: 1;
        visibility: visible;
        transition: opacity 0.3s, visibility 0.3s; } }

.menu {
    &__wrapper {
        width: 670px;
        height: 930px;
        margin: 0px auto;
        padding: 0px; }
    &__items {
        @include clr; }
    &__item {
        height: 290px;
        width: 290px; }
    &__title {
        margin-bottom: 44px; } }

.item {
    display: block;
    @include cover;
    float: left;
    margin-bottom: 30px;
    margin-right: 30px;
    position: relative; }

.item-text {
    position: relative;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
    background-color: $text-bg;
    padding: 6px 20px; }

.item-title-sm {
    font-size: 24px;
    font-weight: 700;
    line-height: 2;
    color: $white; }

.item-title-lg {
    font-size: 36px;
    font-weight: 500;
    line-height: 0.67;
    color: $white; }
